header,
.profile {
	margin-bottom: 1rem;
}
img {
	object-fit: cover;
	max-width: 100%;
}
h1 {
	font-size: 1.5rem;
	color: black;
}
input[type="text"] {
	padding: 1rem;
	border: none;
	background: transparent;
	outline: none;
	color: #626060;
	width: 100%;
	font-size: 1rem;
	font-family: "Roboto", sans-serif;
	font-weight: 100;
}
.home {
	margin: 1rem 0.5rem;
}
.profile {
	color: #626060;
	display: flex;
	justify-content: left;
	align-items: center;
}
.profile-img {
	/* display: block; */
	width: 60px;
	margin-right: 0.5rem;
}
.searchbar {
	display: flex;
	background: #e5e5e5;
	border-radius: 7px;
	align-items: center;
	justify-content: space-between;
	padding: 0 1rem;
}
.grid {
	display: grid;
	grid-template-columns: 100fr 100fr;
	gap: 10px;
}
.block {
	height: 200px;
}
