@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@400;700&display=swap");

.item {
	font-family: "Red Hat Display", sans-serif;
}
.item-image {
	max-height: 120px;
	overflow: hidden;
	display: block;
	border-radius: 10px;
	margin-bottom: 0.5rem;
}
#star {
	width: fit-content;
	border-radius: 50%;
	background: white;
	position: relative;
	left: 140px;
	top: 32px;
}
.item-title {
	color: #0a0a0a;
	font-weight: bold;
	font-size: 1.2rem;
}
.details {
	/* color: #626060; */
	display: flex;
	align-items: center;
}
